import React from 'react'
import styled from 'styled-components'
import { Card } from 'rebass'
import QRCode from 'qrcode.react'

const Container = styled(Card)`
  position: relative;
  width: ${props => props.size}
  height: ${props => props.size}
  display: inline-block;
`
const CropWrapper = styled(Card)`
  position: relative;
  width: ${props => props.size}
  height: ${props => props.size}
`
const TopLeft = styled(Card)`
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  width: ${props => props.size}
  height: ${props => props.size}
  clip-path: polygon(0 0, 20% 0, 20% 20%, 0 20%);
`
const TopRight = styled(Card)`
  position: absolute;
  top: 0;
  right: 0;
  display: inline-block;
  width: ${props => props.size}
  height: ${props => props.size}
  clip-path: polygon(80% 0, 100% 0%, 100% 20%, 80% 20%);
`
const BottomLeft = styled(Card)`
  position: absolute;
  bottom: 0;
  left: 0;
  display: inline-block;
  width: ${props => props.size}
  height: ${props => props.size}
  clip-path: polygon(0 80%, 20% 80%, 20% 100%, 0 100%);
`
const BottomRight = styled(Card)`
  position: absolute;
  bottom: 0;
  right: 0;
  display: inline-block;
  width: ${props => props.size}
  height: ${props => props.size}
  clip-path: polygon(80% 80%, 100% 80%, 100% 100%, 80% 100%);
`
const Code = styled(QRCode)`
  border-style: solid;
  border-color: white;
  border-width: 2px;
  position: absolute;
  top: 10%
  left: 10%
`

/**
 * @render react
 * @name QRCode
 * @example
 * <QRCode value="123456789" />
 */
class ZapQRCode extends React.PureComponent {
  render() {
    const { size } = this.props
    return (
      <Container size={size}>
        <CropWrapper size={size}>
          <TopLeft size={size} border={1} borderRadius="12%" borderColor="lightningOrange" />
          <TopRight size={size} border={1} borderRadius="12%" borderColor="lightningOrange" />
          <BottomLeft size={size} border={1} borderRadius="12%" borderColor="lightningOrange" />
          <BottomRight size={size} border={1} borderRadius="12%" borderColor="lightningOrange" />
        </CropWrapper>
        <Code {...this.props} size="80%" renderAs="svg" bgColor="white" fgColor="black" level="L" />
      </Container>
    )
  }
}

ZapQRCode.displayName = 'QRCode'

ZapQRCode.defaultProps = {
  size: '150px'
}
export default ZapQRCode
